___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary-color: rgb(13, 106, 134);
  --primary-color-darker: rgb(9, 48, 56);
}

* {
  box-sizing: border-box;
  outline: 0;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--primary-color);
  font-family: 'Open sans', sans-serif;
  font-size: 1.3em;
  line-height: 1.5em;
}

.container {
  max-width: 640px;
  margin: 20px auto;
  background-color: #fff;
  border-radius: 15px;
  padding: 20px;
}

form input, 
form label, 
form button {
  display: block;
  width: 100%;
  margin-bottom: 10px;
}

form input {
  font-size: 24px;
  height: 50px;
  padding: 0 20px;
  border-radius: 5px;
}

form input:focus {
  outline: 1px solid var(--primary-color);
}

form button {
  border: none;
  background-color: var(--primary-color);
  color: #fff;
  font-size: 18px;
  font-weight: 700;
  height: 50px;
  cursor: pointer;
  margin-top: 30px;
  border-radius: 5px;
}

form button:hover {
  background-color: var(--primary-color-darker);
}

.container h1 {
  text-align: center;
  margin-bottom: 40px;
}

.cpf-gerado {
  font-size: 2em;
  color: var(--primary-color);
  text-align: center;
  font-weight: bold;
  margin-bottom: 40px;
  letter-spacing: 5px;
